import './App.scss';
import * as Core from './Core';
import * as Views from './Views';

class App
{
    private rootNode: HTMLElement;
    private keyboard: Views.Keyboard;

    constructor(selector: string)
    {
        this.rootNode = document.querySelector(selector);
    }

    run()
    {
        this.keyboard = new Views.Keyboard();
        let cardsBoard = new Core.CardsBoard();
        let cardsBoardView = new Views.CardsBoard(cardsBoard);
        let player = new Core.Player();
        let playerView = new Views.Player(player);
        

        this.keyboard.addActionFromKey('ArrowUp', (e: KeyboardEvent): void => {
            

            let isStep = playerView.MoveTo('center');
            if (isStep) {
                cardsBoardView.newLine();
                if (playerView.getLine() == 'center') {
                    let damage = cardsBoard.getCardLineUp().getDamage();
                    let newPlayerHealts = player.getHealth() - damage;
                    player.setHealts(newPlayerHealts);
                } else if (playerView.getLine() == 'right') {
                    let damage = cardsBoard.getCardLineRight().getDamage();
                    let newPlayerHealts = player.getHealth() - damage;
                    player.setHealts(newPlayerHealts);
                } else if (playerView.getLine() == 'left') {
                    let damage = cardsBoard.getCardLineLeft().getDamage();
                    let newPlayerHealts = player.getHealth() - damage;
                    player.setHealts(newPlayerHealts);
                }
                playerView.update();
            }
        });

        this.keyboard.addActionFromKey('ArrowRight', (e: KeyboardEvent): void => {
            this.keyboard.setBlocked(true);
            let isStep = playerView.MoveTo('right');
            if (isStep) {
                cardsBoardView.newLine();
                if (playerView.getLine() == 'center') {
                    let damage = cardsBoard.getCardLineUp().getDamage();
                    let newPlayerHealts = player.getHealth() - damage;
                    player.setHealts(newPlayerHealts);
                } else if (playerView.getLine() == 'right') {
                    let damage = cardsBoard.getCardLineRight().getDamage();
                    let newPlayerHealts = player.getHealth() - damage;
                    player.setHealts(newPlayerHealts);
                } else if (playerView.getLine() == 'left') {
                    let damage = cardsBoard.getCardLineLeft().getDamage();
                    let newPlayerHealts = player.getHealth() - damage;
                    player.setHealts(newPlayerHealts);
                }
                playerView.update();
            }
            this.keyboard.setBlocked(false);
        });

        this.keyboard.addActionFromKey('ArrowLeft', (e: KeyboardEvent): void => {    
            this.keyboard.setBlocked(true);        
            let isStep = playerView.MoveTo('left');
            if (isStep) {
                cardsBoardView.newLine();
                if (playerView.getLine() == 'center') {
                    let damage = cardsBoard.getCardLineUp().getDamage();
                    let newPlayerHealts = player.getHealth() - damage;
                    player.setHealts(newPlayerHealts);
                } else if (playerView.getLine() == 'right') {
                    let damage = cardsBoard.getCardLineRight().getDamage();
                    let newPlayerHealts = player.getHealth() - damage;
                    player.setHealts(newPlayerHealts);
                } else if (playerView.getLine() == 'left') {
                    let damage = cardsBoard.getCardLineLeft().getDamage();
                    let newPlayerHealts = player.getHealth() - damage;
                    player.setHealts(newPlayerHealts);
                }
                playerView.update();
            }
            this.keyboard.setBlocked(false);
        });


        this.rootNode.appendChild(cardsBoardView.render());
        this.rootNode.appendChild(playerView.render());
    }
}

(function(){
    document.addEventListener('DOMContentLoaded', () => {
        (new App('#app')).run();
    });
})();

export default App;